.Products-table{
    display: table;
    width: 80vw;
    margin: 0 auto;
    text-align: center;    
}
.Products-table-row{
    display: table-row;    
}
.Products-table-cell,
.Products-table-heading{
    display: table-cell;
    padding: 10px;
}

.Products-table-row:nth-child(even){
    background-color: black;
}

.Products-table-heading{    
    font-weight: 700;
    text-align: center;
    background-color: bisque;
    color: black;
}

.Products-header{    
    width: 80vw;
    margin: 0 auto;
    padding: 10px;
    padding-bottom: 20px;
    box-sizing: border-box;
    background-color: black;    
    display: flex;
    justify-content: space-between;
}