.Edit{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 300px;    
    border: 1px solid white;
    padding: 20px;    
    background-color: black;
}

.Edit label,
.Edit input{
    display: block;    
    margin: 0 auto;
}

.Edit label{
    margin-top: 10px;
}

.Edit input{
    width: 100%;
    box-sizing: border-box;
}

.Edit h1,
.Edit h2{
    margin: 0;
    margin-bottom: 10px;
}

.Edit-buttons{    
    margin: 20px auto;
    display: flex;        
}